urger-properties){\r\n  $component-hamburger-properties: map-merge($default-component-hamburger-properties, $component-hamburger-properties);\r\n}\r\n\r\n/*!\r\n * Hamburgers\r\n * @description Tasty CSS-animated hamburgers\r\n * @author Jonathan Suh @jonsuh\r\n * @site https://jonsuh.com/hamburgers\r\n * @link https://github.com/jonsuh/hamburgers\r\n */\r\n\r\n// Settings\r\n// ==================================================\r\n$hamburger-padding-x           : getThemeProperty(hamburger-padding-x, $component-hamburger-properties) !default;\r\n$hamburger-padding-y           : getThemeProperty(hamburger-padding-y, $component-hamburger-properties) !default;\r\n$hamburger-layer-width         : getThemeProperty(hamburger-layer-width, $component-hamburger-properties) !default;\r\n$hamburger-layer-height        : getThemeProperty(hamburger-layer-height, $component-hamburger-properties) !default;\r\n$hamburger-layer-spacing       : getThemeProperty(hamburger-layer-spacing , $component-hamburger-properties) !default;\r\n$hamburger-layer-color         : getThemeProperty(hamburger-layer-color , $component-hamburger-properties) !default;\r\n$hamburger-layer-border-radius : getThemeProperty(hamburger-layer-border-radius, $component-hamburger-properties) !default;\r\n$hamburger-hover-opacity       : getThemeProperty(hamburger-hover-opacity, $component-hamburger-properties) !default;\r\n$hamburger-active-layer-color  : getThemeProperty(hamburger-active-layer-color, $component-hamburger-properties) !default;\r\n$hamburger-active-hover-opacity: getThemeProperty(hamburger-active-hover-opacity, $component-hamburger-properties) !default;\r\n\r\n// To use CSS filters as the hover effect instead of opacity,\r\n// set $hamburger-hover-use-filter as true and\r\n// change the value of $hamburger-hover-filter accordingly.\r\n$hamburger-hover-use-filter   : false !default;\r\n$hamburger-hover-filter       : opacity(50%) !default;\r\n$hamburger-active-hover-filter: $hamburger-hover-filter 